<script>
import api from "../model/api.js"
import vi from 'element-plus/dist/locale/vi.mjs'
import en from 'element-plus/dist/locale/en.mjs'
import { setLanguage, getLanguage } from '@/utils/cookies'

export default
    {
        name: "LayoutPage",
        data() {
            return {
                isCollapse: false,
                leftSideBarWidth: "250px",
                curElLang: "", // current Lang i18n for Element plus
                curLang: "", //current lang i18n for system
                langLs: [
                    { label: "Tiếng Việt", value: "vi-VN" },
                    { label: "English", value: "en-US" }
                ]
            }
        },
        components: {},
        methods: {
            LogOut() {
                api.LogOut((dat) => {
                    console.log(dat)
                    this.$router.push({ name: 'Login', params: {} })
                }, (er) => {
                    console.log(er)
                })
            }
            ,
            ToggleLeftSide() {
                this.isCollapse = !this.isCollapse;
                if (this.isCollapse) this.leftSideBarWidth = "64px"
                else this.leftSideBarWidth = "250px"
            }
            ,
            ChangeLang1(keyLang) {
                this.curLang = keyLang;
                this.ChangeLang();
            },
            ChangeLang() {
                switch (this.curLang) {
                    case "en-US": this.curElLang = en; break;
                    case "vi-VN": this.curElLang = vi; break;
                    default:
                        this.curElLang = vi;
                        break;
                }
                if (this.curLang) {
                    this.$i18n.locale = this.curLang
                    // Cookie
                    setLanguage(this.curLang)
                    // i18n store
                    this.$store.state.curi18n.curLang = this.curLang
                    this.$store.state.curi18n.curElLang = this.curElLang
                }

            },
            InitLang() {
                let currLang = getLanguage();
                this.curLang = currLang ? currLang : "vi-VN";
                this.ChangeLang();
            },

        },
        mounted() {
            // i18n 
            this.InitLang();
        }
    }
</script>
